	module.exports = {"data":{"site":{"siteMetadata":{"title":"weiran.co","author":"Weiran Zhang"}},"markdownRemark":{"id":"/Users/weiran/git/weiran/weiran.co/src/pages/blog/2013/2/android-fragmentation-is-a-security-flaw/index.md absPath of file >>> MarkdownRemark","html":"<blockquote>\n<p>In late October, researchers at North Carolina State University alerted Google to a security flaw that could let scam artists send phony text messages to Android phones — a practice called “smishing” that can ensnare consumers in fraud.</p>\n<p>Google’s security officials replied in minutes, confirming the flaw and promising to correct it. Within days they had incorporated a fix into the latest version of the Android operating system, Jelly Bean 4.2, and made available a security update for earlier versions.</p>\n<p>But for most Android phones, the fix never arrived. For many, it never will.</p>\n</blockquote>\n<p>Because open is always better.</p>","frontmatter":{"title":"Android fragmentation is a security flaw","date":"February 08, 2013","passthroughUrl":"http://www.washingtonpost.com/business/technology/android-phones-vulnerable-to-hackers/2013/02/01/f3248922-6723-11e2-9e1b-07db1d2ccd5b_story.html"}}},"pathContext":{"slug":"/blog/2013/2/android-fragmentation-is-a-security-flaw/","previous":{"fields":{"slug":"/blog/2013/2/whos-being-dishonest-with-storage-space/"},"frontmatter":{"title":"Who's being dishonest with storage space?","passthroughUrl":"http://www.zdnet.com/surface-pro-versus-macbook-air-whos-being-dishonest-with-storage-space-7000011009/"}},"next":{"fields":{"slug":"/blog/2013/2/shops-deny-claims-by-thorsten-heins-that-blackberry-phones-are-selling-out/"},"frontmatter":{"title":"Shops deny claims by Thorsten Heins that BlackBerry phones are selling out","passthroughUrl":"http://www.telegraph.co.uk/technology/9853190/BlackBerry-10-shops-deny-claims-by-Thorsten-Heins-that-Z10-phones-are-selling-out.html"}}}}

